import React from 'react';
import './App.css';
import './index.js';
import ShowHide from './ShowHide.js';

class Demoscena extends React.Component {
  componentDidMount () {
  }

render() {
  return (

            <article>
            <div className='content'>
            W roku 2021, polska demoscena, podobnie jak w wielu innych krajach, zostaa wpisana przez Ministra Kultury na list 
            niematerialnego dziedzictwa kulturowego UNESCO.
            Czym jest demoscena? Demoscena to grupa ludzi, ktrzy tworz niesamowite pokazy komputerowe. 
            
            <br/>
<span id="dots"></span>
<span id="more" className="more">
            <br/>
            Te prezentacje nazywane s "dememi" lub "demonstracjami". Pokazuj w nich, jak potne 
            i efektowne rzeczy mona stworzy na komputerze liczc je na ywo (podobnie jak gry). 
            To tak, jakby tworzyli krtkie filmy, najczciej liczone w czasie rzeczywistym, ktre prezentuj zdolnoci techniczne komputera w dziedzinach, takich 
            jak grafika, dwik i animacja. Demoscena to swojego rodzaju konkurs kreatywnoci i umiejtnoci wrd pasjonatw komputerw.
            <br/>
            Moja historia zwizana z demoscen rozpocza si w roku 1995,
            kiedy mj tato przywiz na dyskietkach dwa dema:
            <br/>
            <br/>
            Future Crew - Second Reality (1993)          

            <div className="video-container">
            <iframe className="video" src="https://www.youtube.com/embed/iw17c70uJes" title="Future Crew - Second Reality (1993) [60fps]" frameBorder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share" allowFullScreen={true}></iframe>            
            </div>
            <br/>
            Crystal Dream 2 by Triton (1993)
            
            <div className="video-container">
            <iframe className="video"src="https://www.youtube.com/embed/7mWbnVPwX4U" title="Triton - Crystal Dream 2 (1993) [60fps]" frameBorder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share" allowFullScreen={true}></iframe>
            </div>
            <br/>

            Po ich obejrzeniu, zachwyciem si i postanowiem za wszelk cen dowidzie si, jak zostay zrobione.
            Co ciekawe, nie miaem podobnych odczu grajc w gry.
            Pomylaem, e najlepszym sposobem na zrozumienie tego, bdzie prba zakodowania czego podobnego.
            <br/>
            <br/>
            W styczniu 1996 (miesic przed demoparty "Prba Generalna II") zaoyem grup o nazwie Norferin.
            Nasza ekipa skadaa si z trzech osb: ja (Zsacul), Dr.Frankenstein i Sir Tomasz. 
            W cigu trzech tygodni stworzylimy nasze pierwsze demo, ktre nazwalimy "The first demo", i pojechalimy je wystawi
            na demoparty.
            Niestety, demo zajo ostanie miejsce, byo do proste (napisane w Pascalu), niezoptymalizowane (nie mielimy wczeniejszego dowiadczenia), a komputer,
            na party by a dwukrotnie wolniejszy od tego, na ktrym je kodowalimy.

      Po party Sir Tomasz zmieni nicka na Juve i wzilimy si do pracy. 
      Wecie pod uwag to, e przed "prb generaln II" widzielimy tylko produkcj z "prby generalnej I" (z niemiertelnego czasopisma CD-Action).
      W maju 1996 rozpoczlimy prac nad demem na "Staszic Compo III". 
      Demo zajo pierwsze miejsce i nosio nazw "Contribution to Nirvana" (niestety byy wystawione tylko dwa dema). <br/>
      Kilka miesicy pniej Lavina (dawniej Kuba) doczy do nas jako muzyk. <br/>
      W listopadzie 1996 pokazalimy nasze kolejne demo o nazwie "Blue Flush", ktre zajo trzecie miejsce na Gravity'96. <br/>
      Tydzie po imprezie do nas doczy Fremen (dawniej Feud) jako programista i swapper. <br/>
      W styczniu '97 (miesic przed "Prb Generaln III") Fremen przenis si do Fortress. <br/>
      Longhi doczy do nas jako Sysop naszego BBS oraz modemowy swapper.<br/>

      Stworzylimy nasze czwarte demo "Everlasting Dimensions" na "Prb Generaln III", ktre zajo czwarte miejsce. 
      Lavina odszed z powodu braku aktywnoci. <br/>
      Zamiast uczy si do matury, przepisywaem silnik graficzny z Pascala na C++.
      Nasze kolejne demo (pierwsze w Watcom C++ 10.5 ze wstawkami w Assemblerze) nosio nazw "Ancient Plate". 
      Pokazalimy je na Intel Ouside IV we Wocawku, zajmujc drugie miejsce. <br/>
      Kolejne demo na Gravity'97, zatytuowane "Trip into your mind" (pierwsze w 16-bitowym kolorze),
      zdobyo trzecie miejsce. <br/>
      Kolejne demo "Technological Impression" zajo pierwsze miejsce na Astrosyn 1997.<br/>
      Nastpne "Potpieni" (kodowane tylko przeze mnie) zajo pierwsze miejsce na Rush Hours 1998.<br/>
      Demo "Ikar" byo czwarte na Quast 1998.<br/>
      "Rain" sidme na Gravity 1998. <br/>
      Ostatnie demo w zeszym stuleciu "Brama" zajo trzecie miejsce na Analogue 1999.<br/>
      <br/>
      Po tym nadszed mj dugi okres przerwy od demosceny, trwajcy a 21 lat. W wolnych chwilach (gwnie podczas 
      wakacji), powoli tworzyem efekty w Unity, zamiast jak dotychczas korzysta z wasnego silnika.
      Okazja do ponownego sprbowania si, nadesza w trakcie pandemii, kiedy okazao si, e na jednym z najwaniejszych demoparty na wiecie, czyli Revision, mona byo zgosi swoje produkcje zdalnie.
      Dowiedziaem si o tym do pno, ale mimo wszystko postanowiem sprbowa. W cigu zaledwie czterech dni udao mi si stworzy wikszo dema.       
      Na wicej zabrako czasu, wic po prostu obciem muzyk i demo z planowanych 4 minut, trwa 2:52.
      Demo "Live The Trance" zajo 15 miejsce (7 od koca) na Revision 2020.
      <br/>
      <br/>

      <div className="video-container">
      <iframe className="video" src="https://www.youtube.com/embed/8QLLSGyxsLc" title="Live The Trance - Norferin Demo at Revision 2020" frameBorder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share" allowFullScreen={true}></iframe>      
      </div>

      <a href="https://drive.google.com/uc?id=1dqNwKjZjJ0DdROXMiZ-sU7QM-6KPJhet">Plik wykonywalny</a>

      <br/>
      <br/>
      Demo zostao przyjte entuzjastycznie, a tworzenie go sprawio mi tak frajd, e zdecydowaem si na reaktywacj grupy Norferin.
      Rozpoczem prac nad nowym demem, tym razem w silniku Unreal 5. 
      Doczyli do mnie Szudi (Muzyka, 3D) i JS29a (dodatkowy kod).
      Po miesicu mocno intensywnej pracy, udao nam si ukoczy kolejn produkcj.
      <br/>
      Demo "ground zero" zajo 5 miejsce (na 10 dem) na Xenium 2023.
      <br/>
      <br/>

      <div className="video-container">
      <iframe className="video" src="https://www.youtube.com/embed/IoL8q9NVjQw" title="Ground Zero - Norferin Demo at Xenium 2023" frameBorder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share" allowFullScreen={true}></iframe>
      </div>

      <a href="https://files.scene.org/get:nl-https/parties/2023/xenium23/pc_demo/demo_norferin_groundzero.zip">Plik wykonywalny (wymaga karty z RTX)</a>
      
      </span>
      <br />
      <br />
      
      <ShowHide showHideMore='dscena_more' dots='dots' more='more'/>
      
      </div>
      </article>
  );
}

}
export default Demoscena;
